.mobile_nav_container {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 999;
    width: 60%;
    height: 100%;
    background-color: #0a0b0c;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .close_btn {
    position: absolute;
    right: 0;
    color: #cae962;
    font-size: 32px;
    margin: 10px 10px 0px 0px;
  }
  
  .nav_link_container {
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 100%;
    margin-top: 100px;
  }
  
  .navbar_link {
    text-decoration: none;
    color: #cae962;
    font-size: 18px;
    padding: 5px 20px;
  }
  
  .navbar_link:hover {
    border-bottom: 1px solid #cae962;
  }
  
  .navbar_link_active {
    text-decoration: none;
    color: #cae962;
    font-size: 18px;
    padding: 5px 20px;
    background-color: #cae962;
    color: #2a2c31;
  }