.favpage_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
  .fav_card_container {
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 40px;
    color: #cae962;
  }
  
  .heading {
    width: 90%;
    padding-top: 10px;
  }
  
  .heading h1 {
    color: rgb(20, 20, 20);
  }
  
  @media screen and (max-width: 768px) {
    .fav_card_container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }